import naja from "naja";

Array.from(document.getElementsByClassName('remove-fieldset')).forEach((element) => {
    element.addEventListener('click', function(event){
        event.preventDefault();
    });
});


document.addEventListener('DOMContentLoaded', function() {
    window.repeater = new FormRepeater({
        container: document.querySelector('[data-repeater="contact"]'),
        // namePattern: 'row[{name}][{index}]',
    });
});

naja.addEventListener('success', (event) => {
   initForms();
});

initForms();
function initForms() {
// najdeme na strnce vechny podzen selectboxy
    document.querySelectorAll('select[data-depends]').forEach((childSelect) => {
        let parentSelect = childSelect.form[childSelect.dataset.depends]; // nadzen <select>
        let url = childSelect.dataset.url; // atribut data-url
        let items = JSON.parse(childSelect.dataset.items || 'null'); // atribut data-items

        // kdy uivatel zmn vybranou poloku v nadzenm selectu...
        parentSelect.addEventListener('change', () => {
            // pokud existuje atribut data-items...
            if (items) {
                // nahrajeme rovnou do podzenho selectboxu nov poloky
                updateSelectbox(childSelect, items[parentSelect.value]);
            }

            // pokud existuje atribut data-url...
            if (url) {
                // udlme AJAXov poadavek na endpoint s vybranou polokou msto placeholderu
                fetch(url.replace(encodeURIComponent('#'), encodeURIComponent(parentSelect.value)))
                    .then((response) => response.json())
                    // a nahrajeme do podzenho selectboxu nov poloky
                    .then((data) => updateSelectbox(childSelect, data));
            }
        });
    });
}
// pepe <options> v <select>
function updateSelectbox(select, items) {
    select.innerHTML = ''; // odstranme ve
    for (let id in items) { // vlome nov
        let el = document.createElement('option');
        el.setAttribute('value', id);
        el.innerText = items[id];
        select.appendChild(el);
    }
}